import React, { useState, useEffect, useContext, useReducer } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Post from '../../components/Post'
import * as network from '../../network'
import clsx from 'clsx'

import { useHistory, useLocation } from "react-router-dom"

import { getPost, deleteLike, saveLike } from '../../network'

const useStyles = makeStyles({
  root: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap"
  },
  post: {
    margin: 10
  }
})

function reducer(state, action) {
  if (action.type === 'error') {
    return {posts: null, error: action.payload}
  }

  let posts
  switch (action.type) {
    case 'posts':
      posts = action.payload
      break
    case 'unlike':
      posts = state.posts.map(post => {
        if (post.id !== action.payload.postId) {
          return post
        }
        return {...post, totalLikes: post.totalLikes-1, liked: false}
      })
      break
    case 'like':
      posts = state.posts.map(post => {
        if (post.id !== action.payload.postId) {
          return post
        }
        return {...post, totalLikes: post.totalLikes+1, liked: true}
      })
      break
    default:
      throw new Error();
  }

  return {
    error: null,
    posts: posts
  }
}

export default function Posts({className}) {
  const classes = useStyles()
  const history = useHistory()

  const [state, dispatch] = useReducer(reducer, {posts: []})

  let location = useLocation()

  useEffect(() => {
    let query = new URLSearchParams(location.search);
    const search = query.get("search")
    ;(async () => {
      try {
        const data = await network.getPosts({search})
        dispatch({type: "posts", payload: data.posts})
      } catch (error) {
        dispatch({type: "error", payload: error.message})
      }
    })()
  }, [location])

  const onLikeClicked = async data => {
    try {
      let post = state.posts.find(post => post.id === data.postId)
      if (post.liked) {
        await deleteLike(data)
        dispatch({type: "unlike", payload: {postId: data.postId}})
        
      } else {
        await saveLike(data)
        dispatch({type: "like", payload: {postId: data.postId}})
      }
    } catch (error) {
      dispatch({type: "error", payload: error.message})
    }
  }

  

  const showDetails = data => {
    history.push(`/posts/${data.postId}`)
  }

  return (
    <div className={clsx(classes.root, className)}>
      { !!state.error ?
      <p>{state.error}</p>  
      :
      state.posts.map(post => (
        <Post key={post.id} className={classes.post} post={post} cardClicked={showDetails} commentClicked={showDetails} likeClicked={onLikeClicked}></Post>
      ))
      }
    </div>
  )
}