import React, { useEffect, useState } from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory
} from "react-router-dom"

import { makeStyles } from '@material-ui/core/styles'


import * as userManager from './userManager'

import SiteHeader from './layouts/SiteHeader'
import Posts from './layouts/Posts'
import SinglePost from './layouts/SinglePost'
import CreatePost from './layouts/CreatePost'
import LoginPage from './layouts/LoginPage'
import ProfilePage from './layouts/ProfilePage'
import { UserContext } from './UserContext'

const useStyles = makeStyles((theme) => ({
  main: {
    marginTop: 100,
    "& > *": {
      margin: "auto"
    }
  }
}))

export default function App() {
  
  const classes = useStyles()

  const [user, setUser] = useState(userManager.getTokenDetails())
 
  useEffect(() => {
    if (!user) {
      userManager.removeToken()
    }
  }, [user])

  function PublicRoute({ path, children }) {
    return (
      <Route path={path}>
        {!user ?
          children
          :
          <Redirect to='/' />
        }
      </Route>
    )
  }
  
  function PrivateRoute({ path, children }) {
    return (
      <Route path={path}>
        {!!user ?
          children
          :
          <Redirect to='/login' />
        }
      </Route>
    )
  }
  
  

  return (
    <Router>
      <UserContext.Provider>
        <SiteHeader user={user} setUser={setUser}></SiteHeader>
        <main className={classes.main}>
          <Switch>
            <PublicRoute path="/login">
              <LoginPage setUser={setUser}></LoginPage>
            </PublicRoute>
            <PrivateRoute path="/newPost">
              <CreatePost user={user}></CreatePost>
            </PrivateRoute>
            <PrivateRoute path="/posts/:postId">
              <SinglePost user={user}></SinglePost>
            </PrivateRoute>
            <PrivateRoute path="/profile">
              <ProfilePage user={user}></ProfilePage>
            </PrivateRoute>
            <PrivateRoute path="/">
              <Posts user={user}></Posts>
            </PrivateRoute>
          </Switch>
        </main>
      </UserContext.Provider>
    </Router>
  )
}