import React, {useState, useEffect, useReducer} from 'react'
import { makeStyles } from '@material-ui/core/styles'
import PostDetails from '../../components/PostDetails'
import clsx from 'clsx'

import { useHistory, useParams } from "react-router-dom"

import { getPost, saveComment, saveLike, deleteLike } from '../../network'

const useStyles = makeStyles({
  root: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap"
  },
  post: {
    margin: 10
  }
})

function reducer(state, action) {
  if (action.type === 'error') {
    return {post: null, error: action.payload}
  }

  let post 
  switch (action.type) {
    case 'post':
      post = action.payload
      break
    case 'comment':
      post = {
        ...state.post,
        totalComments: state.post.totalComments+1,
        comments: [action.payload.comment, ...state.post.comments]
      }
      break
    case 'unlike':
      post = {
        ...state.post, totalLikes: state.post.totalLikes-1, liked: false
      }
      break
    case 'like':
      post = {
        ...state.post, totalLikes: state.post.totalLikes+1, liked: true
      }
      break
    default:
      throw new Error();
  }

  return {
    error: null,
    post: post
  }
}

export default function SinglePost({className}) {
  const [state, dispatch] = useReducer(reducer, {});

  let { postId } = useParams()

  const classes = useStyles()

  useEffect(() => {
    (async () => {
      try {
        const post = await getPost({postId})
        dispatch({type: 'post', payload: post})
      } catch (error) {
        dispatch({type: 'error', payload: error.message})
      }
    })()
  }, [])

  const onSubmitComment = async data => {
    try {
      const result = await saveComment(data)
      dispatch({type: 'comment', payload: result})
    } catch (error) {
      dispatch({type: 'error', payload: error.message})
    }
  }

  const onLikeClicked = async data => {
    try {
      if (state.post.liked) {
        await deleteLike(data)
        dispatch({type: 'unlike'})
      } else {
        await saveLike(data)
        dispatch({type: 'like'})
      }
    } catch (error) {
      console.log(error)
      dispatch({type: 'error', payload: error.message})
    }
  }

  return (
    <div className={clsx(classes.root, className)}>
      { !!state.post ? 
      <PostDetails post={state.post} onComment={onSubmitComment} likeClicked={onLikeClicked} submitComment={onSubmitComment}></PostDetails>
      :
      !!state.error ?
      <p>{state.error}</p>
      :
      <p>Loading...</p>
      }
    </div>
  )
}