import React, { useContext } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import PageHeader from '../../components/PageHeader'
import clsx from 'clsx'

import { useHistory } from "react-router-dom"
import { UserContext } from '../../UserContext'

const useStyles = makeStyles({
  root: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap"
  },
  post: {
    margin: 10
  }
})

export default function SiteHeader({user, setUser}) {
  const history = useHistory()

  const onSignOut = () => {
    console.log("Sign out")
    setUser(null)
    history.push("/")
  }

  return (<PageHeader
    title="Social Something"
    titleClicked={() => history.push("/")}
    signOut={onSignOut}
    user={user}
    onSearch={({search}) => {history.push(`/?search=${search}`)}}
    newPost={() => history.push("/newPost")}
    profile={() => history.push("/profile")}
    login={() => history.push("/login")}
    ></PageHeader>)
}