import axios from 'axios'
import { v4 as uuid } from 'uuid'


import { saveToken, getToken, getTokenDetails, removeToken } from './userManager'

function token() {
  const token = getToken()
  if (!token) {
    return {}
  }
  return { Authorization: `Bearer ${token}` }
}

function http({method, path, params}) {
  console.log(token())
  const headers = {
    ...token(),
    'Accept': 'application/json',
    'Content-Type': 'application/json',
  }
  console.log('headers', headers)
  let promise
  if (method == 'get' || method == 'delete') {
    promise = axios[method](path, { headers })
  } else {
    promise = axios[method](path, params, { headers })
  }

  return promise
  .then(res => res.data)
  .catch(error => {
    throw (error.response.data.error ? Error(error.response.data.error) : error)
  })
}

export const login = ({username, password}) => {
  return http({method: 'post', path: `/api/users/login`, params: {username, password}})
  .then(res => {
    const { accessToken } = res
    console.log(accessToken)
    saveToken(accessToken)
    const tokenData = getTokenDetails()
    if (!tokenData) {
      return null
    }
    return tokenData
  })
}

export const logout = () => {
  removeToken()
  return http({method: 'post', path: `/api/users/logout`})
}

export const signUp = ({email, password, username, image}) => {
  console.log(email, password, image)
  return http({method: 'post', path: `/api/users`, params: { email, password, username }})
  .then(res => {
    const { accessToken } = res
    console.log(accessToken)
    saveToken(accessToken)
    const tokenData = getTokenDetails()
    if (!tokenData) {
      return null
    }
    return tokenData
  })

  const formData = new FormData()
 
  formData.append("avatar", image)
  formData.append('user', JSON.stringify({email, password, username, fullName: "Full name"}))

  return axios.post('/api/users', formData, { headers: {'Content-Type': 'multipart/form-data', ...token()}})
  .then(res => {
    const { accessToken } = res.data
    console.log(accessToken)
    saveToken(accessToken)
    const tokenData = getTokenDetails()
    if (!tokenData) {
      return null
    }
    return tokenData
  })
}

